:root {
  --accent-color: rgb(12, 91, 137); 
  --text-color: black;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: Georgia, 'Times New Roman', Times, serif;
  color: var(--text-color);
  font-size: 20px;
}

header {
  background-image: url(./assets/59-blush-pink-8x10.jpg);
  padding: 10px;
  display: block;
  box-shadow: 0 0 5px var(--accent-color);
  border-radius: 15px;
  display: block;
}

h1 {
  font-size: 50px;
  font-family: Georgia, 'Times New Roman', Times, serif;
  color: var(--accent-color);
}

nav ul {
  display: flex;
  justify-content: right;
  list-style-type: none;
  margin: 0px;  
}

nav a {
  font-weight: bold;
  text-decoration: none;
  padding: 10px;
  display: block;
  justify-content: space-between;
  color: var(--accent-color);
}

.button:hover {
  text-shadow: 1px 1px 2px var(--accent-color);
}

#about-me {
  margin-top: 75px;
  display: flex;
  flex-flow: row wrap;
}

#profile-picture {
  width: 15%;
  height: auto;
  margin: 20px;
  border-radius: 25px;
  box-shadow: 5px 5px 5px var(--accent-color);
}

li a {
  color: var(--accent-color);
}

article {
  border: 5px var(--accent-color) dotted;
  width: 85%;
  margin: auto;
  margin-top: 30px;
}

h2 {
  font-size: 200%;
  color: var(--accent-color);
}

h3 {
  color: var(--accent-color);
}

footer h3 {
  padding: 5%;
}

article div {
  padding: 10px;
  width: 75%;
}

#projects {
  margin-top: 100px;
  margin-left: 25px;
}

#projects ul {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  padding: 0px;
  margin: 50px;
}

.smaller-projects {
  max-width: 25%;
  height: auto;
  padding: 30px;
  box-shadow: 0 0 5px var(--accent-color);
  border-radius: 20px;
  background-image: url(./assets/59-blush-pink-8x10.jpg);
}

#showcase-project {
  width: 75%;
  height: auto;
  padding: 30px;
  box-shadow: 0 0 5px var(--accent-color);
  border-radius: 20px;
  background-image: url(./assets/59-blush-pink-8x10.jpg);
}

#showcase-project:hover {
  box-shadow: 0px 0px 15px var(--accent-color);
}

.smaller-projects:hover {
  box-shadow: 0px 0px 15px var(--accent-color);
}

#projects img {
  max-width: 100%;
  height: auto;
  box-shadow: 0 0 5px var(--accent-color);
}

#projects li {
  margin: 20px;
}

ul {
  list-style-type: none;
  padding-inline-start: 0px;
}

#resume {
  margin-top: 75px;
  margin-left: 25px;
  padding: 10px;
  box-shadow: 0 0 5px var(--accent-color);
  background-image: url(./assets/59-blush-pink-8x10.jpg);
  border-radius: 15px;
}

#resume a {
  color: var(--accent-color);
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

#resume a:hover {
  font-style: oblique;
}

hr {
  border-color: var(--accent-color);
  border-width: 3px;
}

#contact {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: center;
  width: 50%;
  margin-top: 75px;
  margin-bottom: 50px;
  color: var(--accent-color);
}

#contact h2 {
  margin-right: 15px;
}

form {
  margin-left: 1rem;
}

input {
  margin-bottom: 10px;
  border-color: var(--accent-color);
  border-style: double;
  box-shadow: 0 0 2px var(--accent-color);
  margin-left: 1em;
}

textarea {
  border-color: var(--accent-color);
  border-width: 2px;
  box-shadow: 0 0 2px var(--accent-color);
  margin-left: 1em;
}

.subBtn {
  background-image: url(./assets/59-blush-pink-8x10.jpg);
  color: var(--accent-color);
  padding:8px;
  margin: 5px;
  font-size: 90%;
  border-radius: 10%;
}

#resume img {
  width: 75%;
  height: auto;
  padding-left: 12%;
  padding-right: 10%;
}

@media screen and (max-width: 800px) {
  body {
      font-size: 75%;
  }

  #profile-picture {
      width: 50%;
      height: auto;
  }

  #about-me div {
      width: 100%;
  }

  #projects {
      flex-flow: column wrap;
      width: 100%;
      padding: 0;
  }

  #showcase-project {
      max-width: 90%;
      padding: 15px;
  }

  .smaller-projects {
      padding: 15px;
      max-width: 50%;
  }

  footer h3 {
    margin-bottom: 0px;
    padding-bottom: 0px;
  }

  footer ul {
    margin-left: 7em;
  }
}
